<template>
  <div class="contain">
    <div class="image-introduce">
      <img src="../../../static/image/about_community_introduce.png" alt="">
    </div>
    <ul class="contain-text" :data="aboutCommunityList">
      <li class="contain-item" v-for="item in aboutCommunityList" :key="item.id">
        <p v-if="item.com_type === 'text'">{{ item.com_content }}</p>
        <img v-else :src="websiteInfo.imageUrlPre + item.com_content + '_hengban.jpg'" :alt="item.com_content + '_hengban.jpg'">
      </li>
    </ul>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  data () {
    return {

    }
  },
  computed: {
    ...mapGetters({
      aboutCommunityList: 'aboutCommunity',
      websiteInfo: 'websiteInfo'
    })
  }
}
</script>

<style scoped>
.contain {
  box-sizing: border-box;
  padding: 0 10vw;
}
.contain-text{
  box-sizing: border-box;
  padding-bottom: 30px;
}
.image-introduce{
  width: 100%;
  height: 280px;
}
.image-introduce img{
  width: 100%;
  height: 100%;
}
li.contain-item {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 12px 0;
}
li.contain-item p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  letter-spacing: 2px;
  word-spacing: 2px;
  line-height: 2em;
  text-indent: 2em;
  text-align: justify;
}
</style>



